<template>
    <div>
        <div class="wrapper">
            <div id="video-chat"></div>

            <div v-if="loading">
                <h5>Cargando...</h5>
            </div>

            <template v-if="">
                <!-- <div v-else-if="distanceInTime" class="text--center"> -->
                <div v-else-if="false" class="text--center">
                    <h3 class="text--gray text--light margin-bottom--zero">
                        La clase empezar√° en:
                    </h3>
                    <h1>{{ distanceInWords }}</h1>
                </div>
                <div v-else class="text--center">
                    <h2 class="margin-bottom--one">
                        Ya ha llegado la hora de la clase!
                    </h2>
                    <Button @click="start">Empezar!</Button>
                </div>
            </template>
        </div>

        <hr />

        <div v-if="!$auth.user" class="margin-bottom--two">
            <h4 class="margin-bottom--zero">Docente:</h4>

            <div class="teacher">
                <figure class="teacher__picture image">
                    <img :src="teacher.picture" />
                </figure>
                <p class="teacher__name">{{ teacher.full_name }}</p>
            </div>
        </div>

        <div>
            <h4 class="margin-bottom--zero">Detalles:</h4>
            <p>{{ asigment.details }}</p>

            <div class="margin-top--one">
                <File
                    v-for="(file, index) in asigment.files"
                    :key="index"
                    :file="file"
                    readonly
                />
            </div>
        </div>
    </div>
</template>

<script>
import formatDistance from 'date-fns/formatDistance'
import { es } from 'date-fns/locale'

export default {
    props: ['displayName', 'roomName', 'asigment', 'teacher'],

    data: () => ({
        distanceInWords: null,
        distanceInTime: true,
        jitsi: null,
        loading: false,
        started: false,
    }),

    mounted() {
        const date = new Date(this.asigment.date)

        this.setDistance(date)
        setInterval(() => this.setDistance(date), 1000)
    },

    methods: {
        setDistance(date) {
            const now = Date.now()
            this.distanceInTime = Math.max(date - now, 0)
            this.distanceInWords = formatDistance(date, now, { locale: es })
        },

        async start() {
            this.loading = true
            await this.include('https://meet.jit.si/external_api.js')

            try {
                const domain = 'meet.jit.si'
                const options = {
                    roomName: this.roomName,
                    parentNode: this.$el.querySelector('#video-chat'),
                    userInfo: { displayName: this.displayName },
                    devices: {
                        audioInput: '<deviceLabel>',
                        audioOutput: '<deviceLabel>',
                        videoInput: '<deviceLabel>',
                    },
                    onload: () => {
                        this.started = true
                        this.loading = false
                    },
                }

                const jitsi = new JitsiMeetExternalAPI(domain, options)

                jitsi.addEventListener('videoConferenceJoined', () => {
                    //
                })

                this.jitsi = jitsi
            } catch (error) {
                console.error('Failed to setup Jitsi API:', error)
                this.started = false
                this.loading = false
            }
        },

        include: url =>
            new Promise(resolve => {
                const script = document.createElement('script')
                script.onload = () => resolve()
                script.src = url
                script.async = true
                document.head.appendChild(script)
            }),
    },
}
</script>

